<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="myproblem-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="我提报的问题" @click-left="onClickLeft" left-arrow> </van-nav-bar>
      <div class="search-bar">
        <van-search placeholder="搜索问题编号、摘要" v-model="filter.key" />
      </div>
      <van-dropdown-menu class="select-bar">
        <van-dropdown-item v-model="filter.status" :options="statusOptions" />
        <van-dropdown-item :title="filter.beginDate" ref="beginDate">
          <van-datetime-picker
            v-model="beginDate"
            type="date"
            @cancel="onBeginDateCancel"
            @confirm="onBeginDateConfirm"
          />
        </van-dropdown-item>
        <van-dropdown-item :title="filter.endDate" ref="endDate">
          <van-datetime-picker v-model="endDate" type="date" @cancel="onEndDateCancel" @confirm="onEndDateConfirm" />
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <div class="myproblem-content">
      <div class="myproblem-item" @click="handleDetail(item)" v-for="item in list" :key="item.id">
        <div class="item-title">
          <span class="item-num">{{ item.no }}</span>
          <span v-if="item.status == 'wait_assigned'" class="item-status" style="color:#FFB105;">待受理</span>
          <span v-else-if="item.status == 'following_up'" class="item-status" style="color:#019933;">跟进中</span>
          <span v-else-if="item.status == 'pending_review'" class="item-status" style="color:#FFB105;">待审核</span>
          <span v-else-if="item.status == 'close'" class="item-status" style="color:#939496">已关闭</span>
          <span v-else-if="item.status == 'create'" class="item-status">待提报</span>
        </div>
        <div class="item-content">{{ item.description }}</div>
        <div class="item-footer">
          <van-icon name="clock-o" />
          <span class="item-time">计划完成时间：{{ item.finishTime }}</span>
          <span class="item-y-time">{{ item.consumeTime }}min</span>
        </div>
      </div>
    </div>
    <van-loading type="spinner" v-if="loading" class="page-loading" />
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import moment from 'moment'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    header: null,
    filter: {
      id: '',
      key: '',
      status: '',
      pageIndex: 1,
      pageSize: 10,
      beginDate: moment()
        .startOf('years')
        .format('YYYY-MM-DD'),
      endDate: moment()
        .endOf('years')
        .format('YYYY-MM-DD')
    },
    list: [],
    localList: [],
    loading: true,
    statusOptions: [
      { text: '全部', value: '' },
      { text: '待受理', value: 'wait_assigned' },
      { text: '跟进中', value: 'following_up' },
      { text: '待审核', value: 'pending_review' },
      { text: '已关闭', value: 'close' }
    ],
    beginDate: new Date(),
    endDate: new Date()
  }),
  methods: {
    onReady(header, main) {
      this.header = header
      this.headerHeightVW = ((this.header.h + 95) * 100) / this.header.w
      const user = localStorage.getItem('user')
      this.filter.id = JSON.parse(user).id
      this.onRefresh()
      this.loadLocalData()

      setTimeout(() => {
        navigate.back('createProblem')
      }, 600)
    },
    onShow() {
      // this.onRefresh()
      this.loadLocalData()
    },
    onClickLeft() {
      navigate.back()
    },
    onLoadConfig() {
      io.getProblemConfig().then(data => {
        localStorage.setItem('problem_level', JSON.stringify(data.level))
        localStorage.setItem('problem_category', JSON.stringify(data.category))
        localStorage.setItem('problem_flow', JSON.stringify(data.processflow))
        localStorage.setItem('problem_address', JSON.stringify(data.address))
        localStorage.setItem('problem_profession', JSON.stringify(data.profession))
        localStorage.setItem('problem_source', JSON.stringify(data.source))
      })
    },
    loadLocalData() {
      this.localList = []
      db.getMyProblem().then(data => {
        this.localList = data
      })
    },
    onShow() {
      this.onRefresh()
    },
    onRefresh() {
      this.filter.pageIndex = 1
      io.getMyQuestion(this.filter)
        .then(data => {
          this.list = [...this.localList, ...data.list]
          this.filter.pageIndex++
          this.$refs.page.finishRefresh()
          this.loading = false
        })
        .catch(e => {
          this.$toast(e)
          this.$refs.page.finishRefresh()
          this.loading = false
          this.list = this.localList
        })
    },
    onLoad() {
      io.getMyQuestion(this.filter)
        .then(data => {
          this.list = this.list.concat(data.list)
          this.filter.pageIndex++
          if (data.list.length == 0) {
            this.$refs.page.endLoad()
          } else {
            this.$refs.page.updateLoad()
          }
          this.loading = false
        })
        .catch(e => {
          this.$toast(e)
          this.$refs.page.updateLoad()
          this.loading = false
        })
    },
    handleDetail(item) {
      if (item.status == 'create') {
        navigate.open('createProblem', { detail: item.id })
      } else {
        navigate.open('problemdetail', { id: item.id })
      }
    },

    onBeginDateCancel() {
      this.$refs.beginDate.toggle()
    },
    onBeginDateConfirm(val) {
      console.log(val)
      this.filter.beginDate = moment(val).format('YYYY-MM-DD')
      this.$refs.beginDate.toggle()
      this.onRefresh()
    },

    onEndDateCancel() {
      this.$refs.endDate.toggle()
    },

    onEndDateConfirm(val) {
      console.log(val)
      this.filter.endDate = moment(val).format('YYYY-MM-DD')
      this.$refs.endDate.toggle()
      this.onRefresh()
    }
  },
  watch: {
    'filter.status': function(val) {
      this.onRefresh()
    }
  }
}
</script>

